.blog-item{
    width: calc(50%-10px);
    border-radius: 20px;
    margin: auto; //agar tidak terlalu renggang antar blog
    margin-bottom: 20px;
    overflow: hidden;
    box-shadow: 5px 6px 14px rgba(0,0,0,0.20);
    .image-thumb{
        width: 100%;
        height: 200px;
        object-fit: cover;
    }
    .content-detail{
        padding: 16px;

        .title-wrapper {
            display: block;
            justify-content: space-between;
            align-items: center;
            .edit-wrapper {
            display: flex;
            align-items: center;
            .edit {
                font-weight: bold;
                color: #03A9F4;
                cursor: pointer;
                margin: 0px;
                margin-right: 14px;
            }
            .delete{
                font-weight: bold;
                margin: 0px;
                margin-left: 14px;
                color:#E91E63;
                cursor: pointer;
            }
        }
    }


        .title{
            margin: 0px;
            font-size: 20px;
            font-weight: bold;
            color: #313131;
        }
        .author{
            margin: 0px;
            margin-top: 4px;
            font-size: 12px;
            color: grey;

        }
        .body{
            margin: 10px;
            margin-top: 20px;
            font-size: 16px;
            color: #353535;
        }
    }
}