.footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgb(0, 102, 255);
    padding: 20px 40px;
    .logo{
        width: 70px;
        height: 70px;
        object-fit: contain;
    }
    .social-wrapper{
        display: flex;

        .icon-wrapper{
            background-color: white;
            padding: 10px;
            border-radius: 40px;
            margin: 0px 10px;
            .icon-medsos{
                width: 30px;
                height: 30px;
                object-fit: contain;
    
            }
        }
    }
    
}

.copyright{
    background-color: rgb(23, 89, 187);
    padding: 10px 0px;
    p{
        font-size: 14px;
        color: white;
        text-align: center;
        margin: 0px;
    }
}